/* /Users/home123/kiyoshi-database/kiyoshi-app/src/assets/sushiAnimation.css */
body {
  background-color: coral;
}

.content {
  position: absolute;
  top: 25%;
  left: 43%;
  transform: translate(-50%, -50%);
}

.chopstick_1 {
  height: 400px;
  width: 15px;
  background-color: black;
  position: absolute;
  transform: rotate(-12deg);
  border-radius: 20px;
  left: 240px;
  top: -600px;
  animation: slideDown1 2s;
  animation-delay: 3s;
  animation-fill-mode: forwards;
}

.chopstick_2 {
  height: 400px;
  width: 15px;
  background-color: black;
  transform: rotate(-6deg);
  border-radius: 20px;
  position: absolute;
  top: -600px;
  z-index: -1;
  left: 220px;
  animation: slideDown1 2s;
  animation-delay: 3s;
  animation-fill-mode: forwards;
}

.seaweed {
  height: 95px;
  width: 95px;
  background-color: #363F24;
  border-radius: 50%;
  position: absolute;
  left: 10px;
  top: 2px;
}

.rice {
  height: 85px;
  width: 85px;
  background-color: white;
  border-radius: 50%;
  position: absolute;
  left: 5px;
  top: 5px;
}

.rice_grain_1 {
  height: 8px;
  width: 8px;
  border-radius: 50%;
  background-color: #DAF1EE;
  position: relative;
  top: 35px;
  left: 2px;
}

.rice_grain_2 {
  height: 6px;
  width: 6px;
  border-radius: 50%;
  background-color: #DAF1EE;
  position: relative;
  top: 36px;
  left: 3px;
}

.rice_grain_3 {
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-color: #DAF1EE;
  position: relative;
  top: -11px;
  left: 25px;
}

.fish {
  position: absolute;
  height: 60px;
  width: 35px;
  background-color: #FC875E;
  left: 40px;
  top: 13px;
  overflow: hidden;
  border-top-right-radius: 40px;
  border-bottom-right-radius: 40px;
  border: 0px solid #FC875E;
  border-bottom: 0;
}

.veggie_1 {
  background-color: #C8D37A;
  height: 30px;
  width: 30px;
  -moz-border-radius: 30px 0 0 0;
  border-radius: 30px 0 0 0;
  top: -11px;
  left: 11px;
  position: relative;
}

.veggie_2 {
  background-color: #C9C9AE;
  height: 30px;
  width: 30px;
  -moz-border-radius: 0px 0 0 30px;
  border-radius: 0px 0 0 30px;
  top: -11px;
  left: 11px;
  position: relative;
}

.fish_lines_1 {
  width: 75px;
  height: 55px;
  border-radius: 50%;
  -ms-box-shadow: 0px 7px 0 0 #ffffff;
  -o-box-shadow: 0px 7px 0 0 #ffffff;
  -webkit-box-shadow: 0px 7px 0 0 #ffffff;
  box-shadow: 0px 7px 0 0 #ffffff;
  opacity: 0.3;
  left: 0px;
  top: -10px;
  position: absolute;
}

.fish_lines_2 {
  width: 85px;
  height: 45px;
  border-radius: 50%;
  -ms-box-shadow: 0px 7px 0 0 #ffffff;
  -o-box-shadow: 0px 7px 0 0 #ffffff;
  -webkit-box-shadow: 0px 7px 0 0 #ffffff;
  box-shadow: 0px 7px 0 0 #ffffff;
  opacity: 0.3;
  left: -15px;
  top: -33px;
  position: absolute;
  transform: rotate(20deg);
}

@keyframes slideIn {
  from {
    left: 0%;
  }
  to {
    margin-left: 50%;
  }
}

@keyframes rotate {
  from {
    transform: rotate(360deg);
  }
  to {
    transform: rotate(0deg);
  }
}

@keyframes slideDown1 {
  0% {
    top: -600px;
  }
  100% {
    top: -300px;
  }
}

#sushi_2 {
  left: 120px;
  position: relative;
  animation: slideIn 3s;
}

#sushi_3 {
  left: -120px;
  position: relative;
  animation: slideIn 3s;
}

#sushi_2 .veggie_1 {
  background-color: #94A98E;
}

#sushi_2 .veggie_2 {
  background-color: #FDC980;
}

#sushi_2 .fish,
#sushi_3 .fish {
  background-color: #E98375;
}

#sushi_1 .fish {
  transform: rotate(180deg);
  left: 11px;
}

#sushi_1 .veggies .veggie_1 {
  transform: rotate(90deg);
  left: 44px;
}

#sushi_1 .veggies .veggie_2 {
  transform: rotate(-90deg);
  left: 44px;
  background-color: #C5473E;
}

#sushi_2 .rice_grain_1,
#sushi_2 .rice_grain_2 {
  left: 75px;
}

#sushi_2 .rice_grain_3 {
  left: 55px;
  top: -3px;
}

#sushi_3 .rice_grain_2 {
  top: 60px;
  left: 65px;
}

#sushi_3 .rice_grain_1 {
  top: 55px;
  left: 6px;
}

#sushi_3 .rice_grain_3 {
  left: 10px;
  top: 3px;
}

#sushi_1 {
  animation: slideIn 3s;
  position: relative;
}

#sushi_1 .rice,
#sushi_2 .rice,
#sushi_3 .rice {
  animation: rotate 3s;
}

/* Mobile size reduction (below 600px) */
@media (max-width: 600px) {
  .content {
    transform: translate(-50%, -50%) scale(0.5); /* Scale entire animation down */
    transform-origin: center; /* Ensure scaling happens from center */
  }

  .chopstick_1,
  .chopstick_2 {
    height: 200px; /* Half size */
    width: 8px;
    left: 120px; /* Adjusted proportionally */
  }

  .chopstick_1 { left: 130px; }
  .chopstick_2 { left: 110px; }

  @keyframes slideDown1 {
    0% { top: -300px; } /* Adjusted for smaller scale */
    100% { top: -150px; }
  }

  #sushi_2 { left: 60px; } /* Half the spacing */
  #sushi_3 { left: -60px; }
}